<script>
import { getTypeList, addType, editType } from '@/api/vm'
export default {
  data () {
    return {
      form: {
        id: ''
      },
      checkAll: false,
      innerCodeList: [],
      cities: [],
      isIndeterminate: false,
      dataS: {
        pageIndex: '0',
        pageSize: '',
        totalCount: '',
        totalPage: ''
      },
      loading: false,
      showAdd: false,
      showEdit: false,
      formTypeAdd: {
        name: '',
        model: '',
        vmRow: '',
        vmCol: '',
        channelMaxCapacity: '',
        image: ''
      },
      formRules: {
        name: [{ required: true, message: '请输入设备名' }],
        model: [{ required: true, message: '请输入设备编码' }],
        vmRow: [{ required: true, message: '请输入设备行数' }],
        vmCol: [{ required: true, message: '请输入设备列数' }],
        channelMaxCapacity: [{ required: true, message: '请输入设备最大容量' }],
        image: [{ required: true, message: '请上传突破' }]
      },
      formTypeEdit: {
        name: '',
        model: '',
        vmRow: '',
        vmCol: '',
        channelMaxCapacity: '',
        image: '',
        typeId: ''
      },
      fileList: ['1'],
      formRulesEdit: {
        name: [{ required: true, message: '请输入设备名' }],
        model: [{ required: true, message: '请输入设备编码' }],
        vmRow: [{ required: true, message: '请输入设备行数' }],
        vmCol: [{ required: true, message: '请输入设备列数' }],
        channelMaxCapacity: [{ required: true, message: '请输入设备最大容量' }],
        image: [{ required: true, message: '请上传突破' }]
      },
      dialogImageUrl: '',
      dialogVisible: false,
      disabled: false
    }
  },
  methods: {
    handleCheckAllChange (val) {
      if (val) {
        this.cities.forEach((item) => {
          if (!this.innerCodeList.includes(item.innerCode)) {
            // 如果选中的item不在列表中,则加入组里
            this.innerCodeList.push(item.innerCode)
          }
        })
      }
      this.innerCodeList = val ? this.innerCodeList : [] // 将选项加入已选择数组
      this.isIndeterminate = false // 将非全选状态设定为 -
    },
    handleCheckedCitiesChange (value) {
      const checkedCount = value.length // 每次改变传入数组长度
      this.checkAll = checkedCount === this.cities.length // 如果长度等于数组长度则全选
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.cities.length // 长度大于一且小于数组长度状态为-
    },
    async getDateS (id) {
      this.loading = true
      let res = []
      if (id) {
        const data = {
          pageIndex: 1,
          pageSize: 10,
          name: id
        }
        res = await getTypeList(data) // 搜索
      } else {
        res = await getTypeList(this.dataS) // 上一页下一页
      }
      this.cities = res.currentPageRecords
      this.dataS.pageIndex = res.pageIndex
      this.dataS.pageSize = res.pageSize
      this.dataS.totalPage = res.totalPage
      this.dataS.totalCount = res.totalCount
      this.innerCodeList = []
      this.isIndeterminate = false
      this.checkAll = false
      this.loading = false
    },
    async addVmType () {
      try {
        const res = await addType(this.formTypeAdd)
        console.log(res)
        if (res) {
          this.$message.success('添加成功')
          this.clearFn()
        }
      } catch (e) {
        this.$message.error(e.response.data)
      }
    },
    handleDel (file) {
      this.$refs.upImg.clearFiles()
    },
    imgDate (link) {
      this.formTypeAdd.image = link
    },
    clearFn () {
      this.showAdd = false
      this.formTypeAdd = {
        name: '',
        model: '',
        vmRow: '',
        vmCol: '',
        channelMaxCapacity: '',
        image: ''
      }
      this.$refs.addType.resetFields()
      this.$refs.upImg.clearFiles()
    },
    clearFn2 () {
      this.showEdit = false
      this.formTypeEdit = {
        name: '',
        model: '',
        vmRow: '',
        vmCol: '',
        channelMaxCapacity: '',
        image: '',
        typeId: ''
      }
      this.$refs.editType.resetFields()
      this.$refs.editImg.clearFiles()
    },
    eidtVmType (item) {
      console.log(item)
      this.showEdit = true
      this.formTypeEdit.typeId = item.typeId
      this.formTypeEdit.name = item.name
      this.formTypeEdit.model = item.model
      this.formTypeEdit.vmRow = item.vmRow
      this.formTypeEdit.vmCol = item.vmCol
      this.formTypeEdit.channelMaxCapacity = item.channelMaxCapacity

      this.formTypeEdit.image = item.image
      console.log(this.formTypeEdit.image)
    },
    async eidtVmTypePut () {
      try {
        const res = await editType(this.formTypeEdit)
        console.log(res)
        if (res) {
          this.$message.success('添加成功')
          this.clearFn2()
        }
      } catch (e) {
        this.$message.error(e.response.data)
      }
    }
  },
  async created () {
    await this.getDateS()
  }
}
</script>

